                            transition: all .3s ease-in-out;
                            opacity: 0;
                        }
                        .project-title{
                            font-size: 1.8rem;
                            text-transform: uppercase;
                            font-weight: 700;
                            margin-left: 7px;
                            opacity: 0;
                        }
                        .fa-long-arrow-alt-right{
                            position: absolute;
                            bottom: 56px;
                            right: 56px;
                            font-size: 3rem;
                        }
                    }
                }
                
    
                h5{
                    font-weight: 600;
                    font-size: 1.3rem;
                    margin-bottom: 1.4rem;
                }
    
                p{
                    font-weight: 300;
                    font-size: 1rem; 
                    margin-bottom: 1rem;
                }
            }
        }
    }
